.sitemode__dashboard {
  &-intro {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 2rem;
    @media screen and (max-width: 767px) {
      gap: 2rem;
    }
    padding: calc(clamp(1.563rem, 0rem + 4.167vw, 3.125rem)) 0;
    &--content {
      width: calc(50% - 1rem);
      @media screen and (max-width: 767px) {
        width: 100%;
      }
      & .smd-intro-title {
        font-size: calc(clamp(1.25rem, 0.25rem + 2.667vw, 2.25rem));
        margin: 0 0 1.5rem 0;
      }
      & .smd-intro-desc {
        font-size: calc(clamp(0.875rem, 0.786rem + 0.238vw, 1rem));
        margin: 0 0 2.5rem 0;
      }
      & .dashboard__buttons {
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;
        gap: 1rem;
        & a {
          padding: calc(clamp(0.375rem, 0.196rem + 0.476vw, 0.625rem)) calc(clamp(0.875rem, 0.563rem + 0.833vw, 1.313rem));
          background-color: transparent;
          border: 1px solid var(--smd-contrast-color);
          border-radius: calc(clamp(0.125rem, 0.08rem + 0.119vw, 0.188rem));
          cursor: pointer;
          line-height: 1;
          text-align: center;
          color: var(--smd-contrast-color);
          font-weight: 600;
          text-decoration: none;
          transition: background 0.2s ease-in-out;
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 0.5rem;
          & svg {
            width: calc(clamp(0.938rem, 0.714rem + 0.595vw, 1.25rem));
            height: calc(clamp(0.938rem, 0.714rem + 0.595vw, 1.25rem));
          }
          &.solid__btn {
            background-color: var(--smd-contrast-color);
            color: var(--smd-base-color);
            border-color: var(--smd-contrast-color);
            &:hover {
              background-color: transparent;
              color: var(--smd-contrast-color);
              border-color: var(--smd-contrast-color);
            }
          }
          &:hover {
            background-color: var(--smd-contrast-color);
            color: var(--smd-base-color);
            border-color: var(--smd-contrast-color);
            & svg {
              & path {
                fill: var(--smd-base-color);
              }
            }
          }
        }
      }
    }
    &--video {
      width: calc(50% - 1rem);
      @media screen and (max-width: 767px) {
        width: 100%;
      }
      & .sitemode__media-box {
        & .site-mode-cards {
          display: flex;
          gap: 1rem;
          &--item {
            padding: 1rem;
            border: 1px solid var(--smd-seperator-color);
            border-radius: 0.7rem;
            & .sm__card {
              position: relative;
              display: block;
              & input {
                visibility: hidden;
                opacity: 0;
              }
              .btn-toggle {
                position: absolute;
                top: 0.5rem;
                right: 0.5rem;
                width: fit-content;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                --bg: var(--wd-primary-color);
                --btn-bg-inactive: #ffedf1;
                --btn-bg-active: var(--smd-primary-color);
                //--border: #DCDCDC;
                --border-w: 0;
                --btn-thumb: red;
                --btn-w: 1.8rem;
                --btn-h: calc(var(--btn-w) / 2);
                --gap: calc(var(--btn-h) / 10);
                & input[type="checkbox"] {
                  display: none !important;
                }
                .toggle {
                  box-sizing: content-box;
                  width: var(--btn-w);
                  height: var(--btn-h);
                  padding: var(--gap);
                  border-radius: var(--btn-w);
                  background: rgba(0,0,0,0.10);
                  cursor: pointer;
                  position: relative;
                  transition: background 0.3s;
                }
                .toggle::before {
                  content: '';
                  box-sizing: border-box;
                  position: absolute;
                  left: var(--gap);
                  width: var(--btn-h);
                  height: var(--btn-h);
                  border-radius: 50%;
                  background: #999999;
                  transition: transform 0.3s;
                }
                input[type="radio"]:checked + .toggle::before {
                  transform: translateX(calc(var(--btn-w) - var(--btn-h)));
                  background: var(--smd-primary-color );
                }

                input[type="radio"]:checked + .toggle {
                  background: #ffedf1;
                }
              }
                & .sm_select_page-icon {
                  margin-bottom: 1rem;
                  display: inline-flex;
                  background-color: lighten(#FE4773, 30%);
                  padding: 0.5rem;
                  border-radius: 0.6rem;
                  & svg {
                    width: 4rem;
                    height: 4rem;
                  }
                }
                & .sm_select_page-title {
                  color: var(--smd-contrast-color);
                  font-size: 1.2rem;
                  font-weight: 600;
                  line-height: 1.3;
                  margin-bottom: 1rem;
                }
              & .sm_select_page-desc {
                color: var(--smd-contrast-color);
                font-size: 1rem;
                line-height: 1.3;
                margin-bottom: 2rem;
              }
              & .sm_select_page-btn {
                display: flex;
                align-items: center;
                gap: 1rem;
                & button {
                  padding: calc(clamp(0.375rem, 0.196rem + 0.476vw, 0.625rem)) calc(clamp(0.875rem, 0.563rem + 0.833vw, 1.313rem));
                  background-color: transparent;
                  color: var(--smd-contrast-color);
                  border-radius: calc(clamp(0.125rem, 0.08rem + 0.119vw, 0.188rem));
                  cursor: pointer;
                  line-height: 1;
                  border: 1px solid var(--smd-contrast-color);
                  display: flex;
                  align-items: center;
                  justify-content: center;
                  gap: 0.5rem;
                  transition: background-color .2s ease-in-out;
                  &.secondary_btn {
                    background-color: var(--smd-contrast-color);
                    color: var(--smd-base-color);
                    border-color: var(--smd-contrast-color);
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  &-cards {
    padding: calc(clamp(1.875rem, 0.536rem + 3.571vw, 3.75rem)) 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    grid-gap: calc(clamp(1.25rem, 0.58rem + 1.786vw, 2.188rem));
  }
}

.smd-card {
  padding: calc(clamp(1.25rem, 0.938rem + 0.833vw, 1.563rem));
  border: 1px solid var(--smd-seperator-color);
  box-shadow: 0 0 4px 0 rgba(0,0,0,0.05);
  &-icon {
    width: calc(clamp(1.25rem, 0.625rem + 1.667vw, 1.875rem));
    height: calc(clamp(1.25rem, 0.625rem + 1.667vw, 1.875rem));
    margin-bottom: 2rem;
  }
  &-title {
    margin-bottom: 1rem;
    & h2 {
      font-size: calc(clamp(1rem, 0.821rem + 0.476vw, 1.25rem));
      line-height: 1;
      margin: 0;
    }
  }
  &-description {
    min-height: calc(clamp(5rem, 3.214rem + 4.762vw, 7.5rem));
    margin-bottom: 1rem;
    & p {
      font-size: calc(clamp(0.813rem, 0.723rem + 0.238vw, 0.938rem));
      line-height: 1.5;
      margin: 0;
    }
  }
  & .smd-card-seperator {
    border: 1px dashed var(--smd-seperator-color);
    width: 100%;
    height: 1px;
  }
  &-actions {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: calc(clamp(1rem, 0.643rem + 0.952vw, 1.5rem)) 0 0 0;
    & a {
      display: flex;
      color: var(--smd-contrast-color);
      font-weight: 600;
      text-decoration: none;
      &:hover {
        text-decoration: underline;
      }
      & svg {
        width: calc(clamp(1.125rem, 1.036rem + 0.238vw, 1.25rem));
        height: calc(clamp(1.125rem, 1.036rem + 0.238vw, 1.25rem));
      }
    }
  }
}
